img{
  display: block;
}

.card{ 
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  
  width: 380px;
  height: 460px;

  margin-top: 50px;
  margin: 0 auto;

  background: linear-gradient(114.99deg, #471CA9 -0.99%, #5736A3 54.28%, #4B2A99 78.99%);
  box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);

  border-radius: 20px;
}

.card__logo{
  position: absolute;
  top: 20px;
  left: 20px;
}

.card__background{
  position: absolute;

  top: 28px;
  left: 36px;
}

.card__avatar{
  position: relative;

  padding-top: 178px;

  z-index: 1;

  &-img{
    position: relative;
  }


  &::before{
    position: absolute;
    
    content: "";

    bottom: 44px;
    left: -145px;

    width: 100%;
    height: 8px;

    width: 380px;

    background: #EBD8FF;
    box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.06),
                inset 0px -1.71846px 3.43693px #AE7BE3,
                inset 0px 3.43693px 2.5777px #FBF8FF;
  }
}